import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Route, Redirect, Switch } from 'react-router-dom';

import { removeUserSession, getToken, getUserName, getUserId } from './components/shared/utils/Common';

import Login from './components/Login/Login';
import Register from './components/Register/Register';
import Home from './components/Home/Home';
import MainNavigation from './components/shared/components/UIElements/MainNavigation';
import DeviceInfo from './components/Devices/DeviceInfo';

import './App.css';
import DevicesList from './components/Devices/DevicesList';

function App() {

  const [isAuthenticated, setIsAuthenticated] = useState(false);

  const user = getUserName();
  const userId = getUserId();

  const handlerAuthentication = {
    signin(cb) {
      setIsAuthenticated(true);
    },
    signout(cb) {
      setIsAuthenticated(false);
      removeUserSession();
    }
  }

  useEffect(() => {
    if (getToken()) {
      handlerAuthentication.signin();
    } else {
      handlerAuthentication.signout();
    }
  })

  const renderLogin = () => {
    return <Login login={handlerAuthentication.signin} />;
  }

  return (
    <Router>
      <Switch>
        <Route path="/signin" exact>
          {renderLogin}
        </Route>
        <Route path="/signup" exact >
          <Register />
        </Route>
        <Route path="/" exact>
          {isAuthenticated ? <MainNavigation logout={handlerAuthentication.signout} userId={userId} component={<Home user={user} />} /> : renderLogin}
        </Route>
        <Route path="/:id/devices" exact>
          {isAuthenticated ? <MainNavigation logout={handlerAuthentication.signout} userId={userId} component={<DevicesList title={"My Devices"} />} /> : renderLogin}
        </Route>
        <Route path="/device/:id" exact>
          {isAuthenticated ? <MainNavigation logout={handlerAuthentication.signout} userId={userId} component={<DeviceInfo title={"Device Info"} />} /> : renderLogin}
        </Route>
        <Redirect to="/" />
      </Switch>
    </Router>
  );
}

export default App;
